.card {
  border: 2px solid #80e1ff;
  padding: 30px;
  width: 220px;
  border-radius: 30px;
  margin-right: 30px;
  transition: box-shadow 0.3s ease-in-out, transform 0.2s ease-in-out;

  &:hover {
    box-shadow: 10px 14px 30px rgba(0, 0, 0, 0.05);
    transform: translateY(-5px);
    cursor: pointer;
  }

  .favorite {
    position: absolute;
    cursor: pointer;
  }

  span {
    font-size: 20px;
    opacity: 0.5;
  }

  b {
    font-size: 17px;
  }

  h5 {
    font-weight: 400;
    font-size: 14px;
  }
}
