body {
  margin: 0;
  font-family: "Inter", system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(181, 100%, 71%);
}

* {
  font-family: "Inter", system-ui;
}

.wrapper {
  .removebtn {
    opacity: 0.5;
    transition: opacity 0.15s ease-in-out;
    &:hover {
      opacity: 2;
    }
  }
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid hsl(181, 100%, 71%);
  img {
    margin-right: 15px;
  }

  h3,
  p {
    margin: 0;
  }
}

.content {
  h1 {
    margin: 0;
  }
}

.button {
  background: #ffffff;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 8px;
  width: 32px;
  height: 32px;
  cursor: pointer;
}

.search-box {
  border: 1px solid #80e1ff;
  border-radius: 10px;
  padding: 0 15px;

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
  }
}

.logo {
  transition: transform 0.3s ease-in-out;
  &:hover {
    transform: translateY(-5px);
  }
}
.overlay {
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 1;
}
.drawer {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 420px;
  right: 0;
  height: 100%;
  padding: 30px;

  background: #ffffff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);

  .items {
    flex: 1;
    overflow: auto;
    margin-bottom: 20px;
  }

  h2 {
    margin: 0;
  }
}
element.style {
  display: flex;
  flex-direction: column;
}
.cartTotalBlock {
  ul {
    display: block;
    margin-bottom: 40px !important;
  }
  li {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    div {
      flex: 1;
      height: 1px;
      border-bottom: 1px dashed #000000;
      position: relative;
      top: -3 px;
      margin: 0 7px;
    }
  }
}

.korzina {
  margin: 0;
  font-size: 24px;
}

.cartitem {
  border: 2px solid hsl(181, 100%, 71%);
  border-radius: 20px;
  p {
    font-size: 14px;
    margin-left: 10px;
  }
  b {
    font-size: 16px;
    margin-left: 10px;
  }
  .removebtn {
    opacity: 0.5;
    transition: opacity 0.15s ease-in-out;
    &:hover {
      opacity: 2;
    }
  }
  .tovar {
    height: 70px;
    width: 70px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 20px;
    margin-right: 20px;
  }
}

.but1 {
  position: relative;
  width: 100%;
  height: 55px;
  background: rgb(94, 185, 86);
  border-radius: 20px;
  border: 0;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s ease-in-out;

  &:hover {
    background: rgb(23, 95, 17);
  }
}
